import React, { PureComponent } from 'react'
import { withStyles } from '@material-ui/styles'
import DeleteIcon from '@material-ui/icons/Delete'
import styles from './styles/MiniPaletteStyles'

class MiniPalette extends PureComponent {
  constructor(props) {
    super(props)
    this.deletePalette = this.deletePalette.bind(this)
    this.onClickPalette = this.onClickPalette.bind(this)
  }
  deletePalette(e) {
    e.stopPropagation()
    this.props.handleDeletePalette(this.props.id)
  }
  onClickPalette() {
    this.props.goToPalette(this.props.id)
  }
  render() {
    console.log('mini palette')
    const { emoji, colors, classes, paletteName } = this.props
    const miniColorBoxes = colors.map((color) => (
      <div
        className={classes.miniColor}
        style={{ backgroundColor: color.color }}
        key={color.name}
      />
    ))
    return (
      <div className={classes.root} onClick={this.onClickPalette}>
        <DeleteIcon
          className={classes.deleteIcon}
          styles={{ transition: 'all 0.3s ease-in-out' }}
          onClick={this.deletePalette}
        />
        <div className={classes.colors}>{miniColorBoxes}</div>
        <h5 className={classes.title}>
          {paletteName} <span className={classes.emoji}>{emoji}</span>
        </h5>
      </div>
    )
  }
}

export default withStyles(styles)(MiniPalette)
